<template>
    <div class="col-lg-3 mb-4">
  <router-link :to="`/detail-product/${product.id}`">
        <img :src="product.image" class="" style="height: 314px" />
        <h5 class="mt-2 text-dark font-weight-bold">
          {{ product.name }}
        </h5>
  </router-link>
          <div>
          <span class="font-weight-bold"> Thể loại: </span
          >{{ product.category }}
          </div>
        <div>
          <span class="font-weight-bold"> Thời lượng:</span>
          {{ product.time }} phút
        </div>
        <div>
          <span class="font-weight-bold"> Khởi chiếu:</span> {{ product.date }}
        </div>
        <div class="action text-center">
          <button class="btn btn-primary mt-2 mr-3">Like</button>
          <button class="btn btn-danger mt-2" @click="handleUpdateCart()">
            Mua vé
          </button>
        </div>
    </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "Product",
  props: ["product"],
  methods: {
    ...mapActions(["updateCart"]),
    handleUpdateCart() {
      const data = {
        action: "ADD_TO_CART",
        value: this.product,
      };
      this.updateCart(data);
    },
  },
  computed: {
    // handleIMG() {
    //     console.log('this.product', this.product)
    //     return {
    //         ...this.product,
    //         image: `require('@${this.product.image}')`,
    //     }
    // },
  },
};
</script>

