<template>
  <div style="background-color: #fdfcf0">
    <div class="container" style="padding-top: 200px">
      <h2 style="border-bottom: 2px solid #222;padding-bottom:10px;">Nội Dung Phim</h2>
      
      <div class="d-flex" style="margin-top:20px;">
        <div class="product-img">
          <img :src="productDetail.image" alt="" />
        </div>
        <div class="product-des ml-5" style="width:100%">
          <h4 style="border-bottom: 2px solid #222;padding-bottom:10px;">{{ productDetail.name }}</h4>
          
          <div><span class="font-weight-bold">Đạo diễn: </span> {{productDetail.director}}</div>
          <div><span class="font-weight-bold">Diễn Viễn: </span> {{productDetail.actor}}</div>
          <div><span class="font-weight-bold">Thể loại: </span> {{productDetail.category}}</div>
          <div><span class="font-weight-bold">Khởi chiếu: </span> {{productDetail.date}}</div>
          <div class="text-danger"><span class="font-weight-bold">Giá: </span> {{productDetail.price}} VND</div>
          <div><span class="font-weight-bold">Thời lượng: </span> {{productDetail.time}} phút</div>
          <div><span class="font-weight-bold">Ngôn ngữ: </span>{{productDetail.language}}</div>
          <button class="btn btn-danger" @click="handleUpdateCart()">Đặt vé</button>
        </div>
      </div>
        <div class="description pb-5">
            <div class="text-center">Chi tiết | Trailer</div>
            <p>{{productDetail.description}}</p>
        </div>
    </div>
  </div>
</template>

<script>
import { useRoute } from "vue-router";
import { computed } from "vue";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "DetailProduct",
  setup() {
    const route = useRoute();
    const id = computed(() => route.params.id);
    return {
      id,
    };
  },
  computed: {
    ...mapGetters(["products"]),
    productDetail() {
      const product = this.products.filter((product) => product.id == this.id);
      return product[0];
    },
  },
  methods: {
    ...mapActions(["updateCart"]),
    handleUpdateCart() {
      const data = {
        action: "ADD_TO_CART",
        value: this.productDetail,
      };
      this.updateCart(data);
    },
  },
};
</script>

