<template>
  <div class="container" style="padding-top: 200px;">
    <img
      src="https://advserver.cgv.vn/www/images/0ee4280e68c2fc78adcfed0931914a0e.jpg"
      alt=""
    />

    <img src="https://www.cgv.vn/skin/frontend/cgv/default/images/bg-cgv/bg-header-transparent-home.png" alt="">

    <h3 style="margin-top:50px;border-bottom:2px solid #000;padding-bottom:20px;">PHIM ĐANG CHIẾU</h3>
    <div class="row ">
      <Product
        v-for="product in filterProducts"
        :key="product.id"
        :product="product"
      />
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Product from "./Product.vue";

export default {
  name: "HomePage",
  components: {
    Product,
  },
  methods: {
  },
  computed: {
    ...mapGetters(["products", "searchValue"]),
    filterProducts() {
      if (!this.searchValue) {
        return this.products;
      } else {
        const filterName = this.products.filter((item) =>
          item.name.toLowerCase().includes(this.searchValue.toLowerCase())
        );
        if (filterName.length == 0) {
          return this.products.filter((item) => item.price == this.searchValue);
        } else {
          return filterName;
        }
      }
    },
  },
};
</script>

