<template>
  <div class="container" style="padding-top: 200px">
    <div class="row">
      <h5 v-if="cart.length == 0" class="mt-4 mb-4">
        <router-link to="/"> Quay lại </router-link>
      </h5>
      <div class="col-sm-12 col-md-12 col-md-offset-1" v-if="cart.length > 0">
        <h4
          style="
            background-color: #000;
            color: #fff;
            padding: 8px;
            text-align: center;
          "
        >
          THÔNG TIN CHUNG
        </h4>
        <div class="row justify-content-between">
          <div class="">
            <img
              src="https://www.cgv.vn/skin/frontend/cgv/default/images/bg-cgv/icon-profile-cgv.png"
              alt=""
              class=""
            />
          </div>
          <div class="">
            <p>Thẻ Thành Viên</p>
            <img
              src="https://www.barcodesinc.com/generator/image.php?code=9992282618851932&style=196&type=C128B&width=220&height=80&xres=1&font=3"
              alt=""
            />
          </div>
        </div>
        <p><strong>Xin chào bạn</strong></p>
        <p>
          Với trang này, bạn sẽ quản lý được tất cả thông tin tài khoản của
          mình.
        </p>
        <hr />
        <table class="table table-hover">
          <thead>
            <tr>
              <th>Tên phim</th>
              <th>Số vé</th>
              <th>1 vé</th>
              <th></th>
              <th></th>
              <th>Tổng tiền</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in cart" :key="item.id">
              <td>
                {{ item.name }}
              </td>
              <td>
                <div class="btn-group">
                  <input
                    type="text"
                    class="quanlity"
                    :value="item.quantily"
                    disabled
                  />
                </div>
              </td>
              <td class="col-sm-1 col-md-1 text-center">
                <strong> {{ item.price }}đ </strong>
              </td>
              <td class="col-sm-1 col-md-1">
                <button
                  type="button"
                  class="btn-delete"
                  @click="handleDelete(item.id)"
                >
                  Xóa vé
                </button>
                
              </td>
              <td>
                <button
                  type="button"
                  class="btn-delete"
                  @click="handlePlus(item.id)"
                >
                  Mua thêm
                </button>
              </td>
              <td>
                  <strong class="text-danger">{{ subTotal }}đ</strong>

              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "ShoppingCart",
  computed: {
    ...mapGetters(["cart"]),
    subTotal() {
      return this.cart.reduce((total, item) => {
        return total + item.price * item.quantily;
      }, 0);
    },
  },
  methods: {
    ...mapActions(["updateCart"]),
    handlePlus(id) {
      const data = {
        action: "PLUS_PRODUCT",
        value: id,
      };
      this.updateCart(data);
    },
    handleDelete(id) {
      const data = {
        action: "DELETE_PRODUCT",
        value: id,
      };
      this.updateCart(data);
    },
  },
};
</script>

<style>
.quanlity {
  width: 70px;
  border-radius: 0px;
  text-align: center;
  border: none;
  font-weight: bold;
}

.btn-delete {
  background: #2f95ec;
  border-radius: 5px;
  text-align: center;
  padding: 3px 20px;
  color: #fff !important;
  font-size: 12px;
}
</style>
